<template>
  <div class="wiki-mine-message">
    <el-table :data="tableData" style="width: 100%" max-height="500">
      <el-table-column fixed prop="send_time" label="日期" width="200"></el-table-column>
      <el-table-column prop="send_username" label="用户名" width="120"></el-table-column>
      <el-table-column prop="content" label="内容" width="500"></el-table-column>
      <el-table-column fixed="right" label="操作" width="445">
        <template slot-scope="scope" width="200">
          <el-button
            @click.native.prevent="seecomment(scope.$index, tableData)"
            type="text"
            size="small"
          >查看消息</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="prev, pager, next"
      :total="pageNum"
      :page-size="8"
      @current-change="togglePage"
    ></el-pagination>
  </div>
</template>

<script>
import * as ajax from '@/api'
import {mapMutations} from 'vuex'

export default {
  created() {
    ajax.getMyMessageList(1).then(resp => {
      if(resp.data.code){
        this.tableData = resp.data.data.message_info
        this.pageNum = resp.data.data.page_info.totalPage * 8;
      }
    });
  },
  data(){
    return{
      tableData:[],
      pageNum: 8
    }
  },
  methods:{
    seecomment(index,data){
      this.messagemodal_true(data[index])
    },
    ...mapMutations(['messagemodal_true']),
    togglePage(currentPage) {
      ajax.getMyCommentList(currentPage).then(resp => {
        if (resp.data.code) {
          this.tableData = resp.data.data.message_info;
          this.pageNum = resp.data.data.page_info.totalPage * 8;
        }
      });
    }
  }
};
</script>

<style lang='scss' scoped>
html,body{
  height: 100%;
  background-color: #545c64;
}

.wiki-mine-message{
  .el-table{
    .cell{
      overflow: hidden;
      text-overflow:ellipsis;
      white-space: nowrap;
    }
  }
  .el-pagination{
    text-align: center;
    margin-top: 20px;
  }
}
</style>
